body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

.header{
  background-color: #F55A5A;
  height: 55px;
  padding: 0 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  color: #fff;
  font-size: 22px;
}
.main{
  padding: 60px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}
.main .card{
  display: flex;
  align-items: center;
  gap: 20px;
  width: 50%;
}
.main .card .card-info .card-info-location{
  display: flex;
  align-items: center;
  gap: 5px;
}
.main .card .card-info .location-icon{
  width: 10px;
}
.main .card .card-info .maps-link{
  color: #918E9B;
}
.main .card .card-info .card-info-title{
  font-size: 26px;
  font-weight: bold;
  margin: 5px 0 20px 0;
}
